import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";

export default class apiFetch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      favourites: [],
      heart: false,
      message: "",
    };

    this.getPlanets = this.getPlanets.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  getPlanets() {
    fetch("https://assignment-machstatz.herokuapp.com/planet")
      .then((res) => res.json())
      .then((res) => {
        console.log(res);
        const posts = res;
        this.setState({
          posts: posts,
        });
      })

      .catch((error) => {
        console.log("There was a Problem" + error);
      });
  }

  componentDidMount() {
    this.getPlanets();
  }

  handleClick = (post) => {
    const isExist = this.state.favourites.includes(post);

    if (isExist) {
      const temp = this.state.favourites.filter((val) => val !== post);
      this.setState({
        favourites: temp,
        heart: !this.state.heart,
        message: post + " Removed from Favourites",
      });
    } else {
      this.setState({
        favourites: [...this.state.favourites, post],
        message: post + " Added to Favourites",
      });
    }
    setTimeout(() => this.setState({ message: "" }), 2000);
  };

  render() {
    console.log(this.state.favourites);
    var heart = "fa fa-heart filled";
    var favouritesMessage =
      this.state.favourites.length > 0 ? (
        ""
      ) : (
        <p className="blue-color">
          Oops! Your Favourites are None.
          <br />
          Click on the Heart to Add
        </p>
      );

    return (
      <Container fluid className="text-center mt-5 ">
        <h1 className="mb-3">Favourite Planet Application Welcomes You</h1>
        <h5 className="blue-color">
          * * Click on the Planet to Explore about it. * *
        </h5>
        <Row className="mt-5 ">
          <Col className="column col-lg-6 col-md-6 col-sm-12 col-xs-12">
            <h1 className="divider mb-5">List of Planets</h1>
            <ul>
              {this.state.posts.map((post) => (
                <li key={post.id}>
                  <a
                    href={`https://en.wikipedia.org/wiki/${post.name}`}
                    target="blank"
                  >
                    {post.name}{" "}
                  </a>
                  {/* <i onClick={() => this.handleClick(post.name)} className="fa fa-heart {this.state.favourites.includes(post.name) ? filled : false}" aria-hidden="true"></i> */}
                  <i
                    onClick={() => this.handleClick(post.name)}
                    className={
                      this.state.favourites.includes(post.name)
                        ? heart
                        : "fa fa-heart none"
                    }
                    aria-hidden="true"
                  ></i>
                </li>
              ))}
            </ul>
          </Col>

          <Col className="column col-lg-6 col-md-6 col-sm-12 col-xs-12">
            <h1 className="divider mb-5">Your Favourite Planets</h1>
            {favouritesMessage}
            {this.state.favourites.length > 0 && (
              <ul>
                {this.state.favourites.map((post) => (
                  <li key={post}>{post}</li>
                ))}
              </ul>
            )}
          </Col>
        </Row>
        <div className="status">{this.state.message}</div>
      </Container>
    );
  }
}
